* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.outer-main-div {
  margin: 0;
  padding: 0;
}
.main-tag {
  margin-top: 50px;
  margin-left: 0px;
  font-family: "ubuntu";
  background: linear-gradient(
    rgb(255, 255, 255) 0%,
    rgb(228, 238, 240) 50%,
    rgb(138, 147, 160) 100%
  );
  z-index: -1;
}
.side-main-navbar {
  background-color: rgb(0, 14, 20);
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  transition: opacity 1000ms ease-in-out;
}
.navbar-link {
  font-size: larger;
  color: rgb(75, 73, 73);

  text-align: center;
  text-transform: uppercase;
  font-family: sans-serif;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.navbar-link a {
  text-decoration: none;
  color: inherit;
}
.navbar-link:hover {
  color: rgb(168, 176, 179);
  font-size: larger;
  transition: 2s;
}

