.project-card-grid {
  z-index: 1000;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-shrink: 0;
  flex-grow: 0;
  justify-content: center;
  transition: 0s;
}
.project-card {
  margin: 25px;
  width: 350px;
  height: 350px;
  border: solid black;
  padding: 25px;
  box-shadow: black 1px 1px 1px;
  transition: 1s;
}
.project-card:hover {
  box-shadow: black 5px 3px 5px;
  transition: 1s;
}
.project-card:active {
  box-shadow: black 15px 3px 15px;
  transition: 1s;
}
