.outer-flex-box {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  flex-shrink: 0;
}
.main-resume-container {
  display: flex;
  flex-direction: column;
  margin: 10px;
}
.main-resume-seperate-links-col {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.first-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 10px;
  transition: 1s;
}

.first-row-first-col {
  margin: 10px;
  max-width: 300px;
  min-width: 150px;
}

.first-row-second-col {
  margin: 10px;
  max-width: 300px;
}
.second-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 10px;
}

.second-row-first-col {
  margin: 10px;
  max-width: 300px;
}
.second-row-second-col {
  margin: 10px;
  max-width: 300px;
}
.third-row {
  padding: 30px;
  transition: 1s;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.image-link {
  margin-right: 10px;
  max-width: 30px;
  margin-bottom: 15px;
  transition: 2s;
}
.image-link:hover {
  max-width: 35px;
  transition: 2s;
}
h4 {
  margin: 15px 5px 0px 0px;
}
.download-button {
  background-color: inherit;
  outline: none;
  padding: 10px;
  border: solid rgb(86, 86, 253) 3px;
  color: rgb(57, 57, 168);
  border-radius: 25px;
  width: 90px;
  transition: 0.5s;
  font-weight: bold;
  margin-top: 10px;
  margin: auto;
}
.download-button:hover {
  border: solid rgb(146, 146, 240) 4px;
  color: rgb(84, 84, 155);
  transition: 0.5s;
}
